:root {
  --primary-color: black;
  --secondary-color: white;
  --menu-text-justification: left;
  --dropdown-list-alignment: center;
  --button-padding: .5rem 1rem;
  --button-gap: .5rem;
  --font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  --font-size: 1rem;
  --hover-background: #444;
  --component-width: 200px;
  --dropdown-padding-top-bottom: .5rem;
  --dropdown-padding-left-right: 1rem;
  --dropdown-gap: .5rem;
  --dropdown-list-padding: 8px;
}

html {
  font-family: var(--font-family);
}
.dropdown-component {
  position: relative;
  box-sizing: border-box;
  width: var(--component-width);
}
.dropdown-menu-button {
  width : 100%;
  display: inline-flex;
  align-items: center;
  justify-content: var(--menu-text-justification);
  padding : var(--button-padding);
  gap : var(--button-gap);
  outline: none;
  border: 0px;
  background-color: var(--primary-color);
  color: var(--secondary-color);
  font-size: var(--font-size);
  font-family: var(--font-family);
}

.drop-down.hidden {
  display: none;
}
.drop-down {
  position: absolute;
  box-sizing: border-box;
  min-width: var(--component-width);
  display:block;
  background-color: var(--primary-color);
  color: var(--secondary-color);
  text-align: var(--dropdown-list-alignment);
  cursor: pointer;
  padding : var(--dropdown-padding-top-bottom) var(--dropdown-padding-left-right); 
  gap : var(--dropdown-gap);
}

.drop-down li {
  padding: var(--dropdown-list-padding);
  cursor: pointer;
}

.drop-down li:hover {
  background-color: var(--hover-background);
}`, "",{"version":3,"sources":["webpack://./src/styles/dropdown-styles.css"],"names":[],"mappings":"AAAA,wBAAwB;;AAExB;EACE,sBAAsB;EACtB,wBAAwB;EACxB,+BAA+B;EAC/B,iCAAiC;EACjC,4BAA4B;EAC5B,mBAAmB;EACnB,8DAA8D;EAC9D,iBAAiB;EACjB,wBAAwB;EACxB,wBAAwB;EACxB,oCAAoC;EACpC,mCAAmC;EACnC,qBAAqB;EACrB,4BAA4B;AAC9B;;AAEA;EACE,+BAA+B;AACjC;AACA;EACE,kBAAkB;EAClB,sBAAsB;EACtB,6BAA6B;AAC/B;AACA;EACE,YAAY;EACZ,oBAAoB;EACpB,mBAAmB;EACnB,+CAA+C;EAC/C,+BAA+B;EAC/B,uBAAuB;EACvB,aAAa;EACb,WAAW;EACX,sCAAsC;EACtC,6BAA6B;EAC7B,2BAA2B;EAC3B,+BAA+B;AACjC;;AAEA;EACE,aAAa;AACf;AACA;EACE,kBAAkB;EAClB,sBAAsB;EACtB,iCAAiC;EACjC,aAAa;EACb,sCAAsC;EACtC,6BAA6B;EAC7B,0CAA0C;EAC1C,eAAe;EACf,+EAA+E;EAC/E,yBAAyB;AAC3B;;AAEA;EACE,qCAAqC;EACrC,eAAe;AACjB;;AAEA;EACE,yCAAyC;AAC3C","sourcesContent":["/* dropdown-styles.css */\n\n:root {\n  --primary-color: black;\n  --secondary-color: white;\n  --menu-text-justification: left;\n  --dropdown-list-alignment: center;\n  --button-padding: .5rem 1rem;\n  --button-gap: .5rem;\n  --font-family: \"Segoe UI\", Tahoma, Geneva, Verdana, sans-serif;\n  --font-size: 1rem;\n  --hover-background: #444;\n  --component-width: 200px;\n  --dropdown-padding-top-bottom: .5rem;\n  --dropdown-padding-left-right: 1rem;\n  --dropdown-gap: .5rem;\n  --dropdown-list-padding: 8px;\n}\n\nhtml {\n  font-family: var(--font-family);\n}\n.dropdown-component {\n  position: relative;\n  box-sizing: border-box;\n  width: var(--component-width);\n}\n.dropdown-menu-button {\n  width : 100%;\n  display: inline-flex;\n  align-items: center;\n  justify-content: var(--menu-text-justification);\n  padding : var(--button-padding);\n  gap : var(--button-gap);\n  outline: none;\n  border: 0px;\n  background-color: var(--primary-color);\n  color: var(--secondary-color);\n  font-size: var(--font-size);\n  font-family: var(--font-family);\n}\n\n.drop-down.hidden {\n  display: none;\n}\n.drop-down {\n  position: absolute;\n  box-sizing: border-box;\n  min-width: var(--component-width);\n  display:block;\n 