*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --dd-font-size: 1rem;
  --hover-background: #444;
  --dd-width: 200px;
  --ddhover-color:#444;

}
html {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
h1 {
  font-size: 1.5rem;
  font-weight: 500;
  margin-bottom: .5rem;
}
.dropdowns {
  width: 50%;
  margin: 0 auto;
  background-color:rgba(160, 137, 99, 1);
  padding: 2rem;
}
.carousels {
  width: 50%;
  margin: 0 auto;
  background-color: #C9B194 ;
  padding: 2rem;
}
.togglemenubutton {
  background-color: rgba(219, 219, 219, 1);
  color: black;
  padding: 1rem;
  font-size: 1rem;
  font-weight: 500;
  border-radius: 25px;
  border: none;
  margin: 1rem;
  cursor: pointer;
}
.togglemenubutton:hover {
  background-color: rgba(219, 219, 219, 0.5);
}
.menu-container {
  display: flex;
  flex-wrap: wrap;
  gap: 1px;
}
.ddmenu {
  position: relative;
  z-index: 2;
}
.ddmenu.hidden {
  display: none;
}
.ddmenu button {
  width: var(--dd-width);
  font-size: var(--dd-font-size);
  display: flex;
  align-items: center;
  background-color: rgba(0,0,0,1);
  color: white;
  padding: .5rem;
  gap: .5rem;
  border: none;
}

.ddmenu a {
  display: block;
  box-sizing: border-box;
  min-width: var(--dd-width);
  color: white;
  padding: 1rem;
  text-decoration: none;
  text-align: left;
  cursor: pointer;
}

.ddmenu .menucontent {
  display: none;
  position: absolute;
  background-color: rgb(91, 91, 91);
}

.ddmenu:hover .menucontent {
  display:block;
}

.ddmenu a:hover,
.ddmenu button:hover {
  background-color: var(--ddhover-color);
}
.carousel-container {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 80%;
  height: 50vh;
  background-color: rgba(160, 137, 99, 1);
  margin: 0 auto;
  padding: 3rem 1rem;
  align-items: center;
}
.carousel-card {
  background-color: azure;
  width: 0;
  height: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 25px;
  border:3px solid #A08963;
  overflow: hidden;
  opacity: 0;
  transition: width 0.5s, opacity 1s;
}

.carousel-card img{
  width: 100%;
  height: 100%;
  object-fit: cover; 
  object-position: center top;
}
.carousel-card-selected {
  background-color: azure;
  width: 75%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 25px;
  border:3px solid #A08963;
  opacity: 1;
  overflow: hidden;
  
}
.button-left {
  position: absolute;
  left: 1rem;;
  top: 45%;
  cursor: pointer;
  background-color: rgba(219, 219, 219, 1);
  border: none;
  padding: .5rem;
  border-radius: 50%;
}
.button-right {
  position: absolute;
  right: 1rem;
  top: 45%;
  cursor: pointer;
  background-color: rgba(219, 219, 219, 1);
  border: none;
  padding: .5rem;
  border-radius: 50%;
}
.button-left:hover,
.button-right:hover{
  background-color: rgba(219, 219, 219, 0.5);
}
.carousel-position-indicators {
  display: flex;
  gap: 2rem;
}
.carousel-position-indicators {
  display: flex;
  gap: 2rem;
}

input[type="radio"] {
 appearance: none;
 width: 10px;
 height: 10px;
 border-radius: 50%;
 outline: none;
 background:rgba(219, 219, 219, 1);
 cursor: pointer;
}
input[type="radio"]:checked {
 appearance: none;
 width: 10px;
 height: 10px;
 border-radius: 50%;
 outline:none; 
 background:rgba(0,0,0,0.5);
 cursor: pointer;
}

